<template lang="html">
  <x-header>
    {{team.name}}
    <a slot="right" @click="joinTeam" href="javascript:;">
      加入队伍
    </a>
  </x-header>
  <div class="team_logo">
    <div class="logo">
      <img :src="team.logo" alt="" />
      <input type="file" accept="image/jpg,image/jpeg,image/png,image/gif" multiple />
    </div>
  </div>
  <div class="weui_cells">
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-at"></i>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{team.name}}</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-tag"></i>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{team.sign}}</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
  </div>
  <div class="weui_cells_title">{{team.name}}的球员：</div>
    <div class="weui_cells">
      <div class="weui_cell" v-for="user in team.users">
        <div class="weui_cell_hd">
          <img :src="user.avatar" style="width:20px;margin-right:5px;display:block">
        </div>
        <div class="weui_cell_bd weui_cell_primary">
          <p>{{user.name}}</p>
        </div>
        <div class="weui_cell_ft">身高{{user.height}}cm</div>
      </div>
    </div>
  </div>
  <toast :show.sync="loadFail" type="text">加载失败</toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Toast from 'vux/dist/components/toast'
  import * as teamService from '../../services/teamService'
  import * as teamUserService from '../../services/teamUserService'

  export default {
    data: function () {
      return {
        team: {
          users: []
        },
        loadFail: false,
        joinFail: false
      }
    },
    route: {
      data (transition) {
        teamService.getTeam(this.$route.params.tid)
        .then((responseJson) => {
          // 登录成功
          this.$set('team', responseJson.team)
        })
        .catch((error) => {
          // 登录失败
          console.log(error)
          this.$set('loadFail', true)
        })
      }
    },
    methods: {
      joinTeam () {
        let token = localStorage.getItem('ruzun_user_token')
        if (token === null) {
          this.$route.router.go({name: 'signin'})
          return
        }
        teamUserService.createTeamUser({team_id: this.$route.params.tid}, token)
        .then((responseJson) => {
          // 加入成功
          this.team.users.append(responseJson.user)
        })
        .catch((error) => {
          // 加入失败
          console.log(error)
          this.$set('joinFail', true)
        })
      }
    },
    components: {
      XHeader,
      Toast
    }
  }
</script>

<style lang="scss">
</style>



